<template>
  <div id="app">
    <div class="container is-fluid">

        <div class="control">
          <label class="radio"><input type="radio" value="woorde1" name="keuse" v-model="keuse" >Woorde_1</label>
          <label class="radio"><input type="radio" value="woorde2" name="keuse" v-model="keuse" >Woorde_2</label>
          <label class="radio"><input type="radio" value="woorde3" name="keuse" v-model="keuse" >Woorde_3</label>
          <label class="radio"><input type="radio" value="woorde4" name="keuse" v-model="keuse" >Woorde_4</label>
          <!-- <label class="radio"><input type="radio" value="woorde5" name="keuse" v-model="keuse" >Woorde_5</label> -->
          <label class="radio"><input type="radio" value="syfers"  name="keuse" v-model="keuse" >Syfers</label>
          <label class="radio"><input type="radio" value="alfabet" name="keuse" v-model="keuse" >Alfabet</label>
          <label class="radio"><input type="radio" value="somme" name="keuse" v-model="keuse" >Somme</label>
        </div>


      <div class="columns">
        <div class="column ">

          <div class="box box-small">
            <img src="./assets/img/getalle-lyn.jpg" class="getalle-lyn-prent" v-if="keuse=='somme'">
        

            <div class="">
                <p class="is-size-1  has-text-success has-text-centered padd">{{ woord }}</p>
            </div>
            <div class="has-text-centered">
              <a class="button is-link is-large is-size-2 is-rounded" @click="getRandomInt">volgende</a>
            </div>
          </div>
         
         
        </div>
      </div>
    </div>
      
  </div>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      alfabet : ['a','b','c','d','e','f','g','h','i','j','k','l','m','n','o','p','q','r','s','t','u','v','w','x','y','z'],
      syfers : ['1','2','3','4','5','6','7','8','9','0'],
      operators : ["+","-"],
      woorde1 : ["lap","lam","bal","kam","kap","mak","pak","dak","bak","tak","tam","tap",
                  "mat","kat","lat","dat","kap","pak","kam","mak","pot","mot","bot","rot",
                  "om","dom","kom","bom","op","pop","kop","tol","lat","dak","lap","bak",
                  "dan","kan","pan","man","om","som","kom","bom","nat","mat","lat","tak"],

      woorde2 : ["hek","hok","hik","hak","laf","dof","lek","mes","dek",
                 "sif","sug","kul","muf","hef","het","hom","hop","hen",
                 "les","dag","nag","ruk","nog","gas","vas","rak","tog",
                 "bes","sap","nes","tog","ram","vin","min","val","pen" ],

      woorde3 : ["kyk","lyk","ryk","wyk","ruk","wek","wag","ruk","ril","rak",
                 "wip","weg","was","wit","wen","wal","buk","nul","ram","rat",
                 "wat","wag","wil","lus","kus","bus","mus","juk","rot","res",
                 "buk","bul","rug","reg","bog","las","res","rem","vat","vet"],
      woorde4 : ["kaas","saam","baas","daar","aas","gaar",
                 "gaan","gaap","haas","jaar","kaal","laat",
                 "jaar","kaas","laat","laag","maag","maan",
                 "aap","raas","saag","saal","taal","taak","vaak",
                 "kas kaas","dam daar","kam, bas, baas",
                 "lat laat","mag maag","tak, taak, man",
                 "maan, sal, saal","lag laag", "as, aas, sag"],

      woorde5 : ["woord5"],
      woord : 'woorde',
      keuse : "woorde1",
      output : []
    }
  },
  methods :{
     getRandomInt : function(){
       console.log("logging")
       if (this.keuse == "woorde1"){
         this.output = this.woorde1}    

       if (this.keuse == "woorde2"){
         this.output = this.woorde2}  

       if (this.keuse == "woorde3"){
         this.output = this.woorde3} 

       if (this.keuse == "woorde4"){
         this.output = this.woorde4}

       if (this.keuse == "woorde5"){
         this.output = this.woorde5}    
       if (this.keuse == "syfers"){
           this.output = this.syfers}
       if (this.keuse == "alfabet"){
           this.output = this.alfabet}

      if(this.keuse != "somme"){
        this.woord = this.output[Math.floor(Math.random() * (this.output.length-1 - 0 + 1)) + 0]
        } 
        else {
            var links =this.syfers[Math.floor(Math.random() * (this.syfers.length-1 - 0 + 1)) + 0]
            var regs  =this.syfers[Math.floor(Math.random() * (this.syfers.length-1 - 0 + 1)) + 0]
            var operator  =this.operators[Math.floor(Math.random() * (this.operators.length-1 - 0 + 1)) + 0]
            this.woord = links + " " + operator + " " + regs
        }
    }
  }
}
</script>

<style>

@font-face {
    font-family: 'cami_primaryregular';
    src: url('assets/font/camipri-webfont.woff2') format('woff2'),
         url('assets/font/camipri-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
  font-family: cami_primaryregular;
}

.padd {
  margin-top: 5vh;
  font-size : 8rem !important;
  font-family: cami_primaryregular;
}

.b{
  display: block;
}

img.getalle-lyn-prent {
    padding-top:5vh;
    display: block;
    margin: 0 auto;
}


</style>



